.navbar {
  background: radial-gradient(rgb(251, 251, 251), rgb(70, 58, 32));
  min-height: 50px;
  position: fixed; 
  margin-top: 0;
  top: 0px;
  right: 0px;
  width: 100%;
  color: rgb(59, 61, 59);
  font-size: calc(10px + 14 * (100vw - 320px)/ 800);
  z-index: 99;
  white-space: nowrap;
}

.navbar ul {
  margin: 0;
  padding: 0;
}

.navbar li {
  display: inline-block;
  padding-right: 26px;
  padding-bottom: 21px;
  width: 33%;
  text-align: center;
}

.brand {
 font-size: 40px;
 font-weight: bold;
}

.container{
  display: flex;
  width: 100%;
  margin-top: auto, 0;
  align-self: center;
}