.card {
  background: rgb(245, 0, 0);
  margin-top: 200px;
  margin-left: 50px;
  border: 20px;
  border-color: black;
  border-radius: 10px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 200px;
  box-shadow: 0 6px 12px rgb(98, 98, 98), 0 3px 6px rgb(85, 85, 85);
  text-align: left;
}

.card > .img-container {
  border-radius: 10px;
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: radial-gradient(rgb(72, 181, 201), rgb(230, 169, 40));
}

.card > .img-container > img {
  width: 80%
}

.card > .img-container:hover {
  /* transform: rotate(360deg); */
  animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
  transform: translate3d(0, 0, 0);
  transition: all 1s;
  /* background: radial-gradient(rgb(243, 9, 87), rgb(39, 31, 14)); */
}
@keyframes shake {
  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }
  
  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}